interface IItem {
    evt: string,
    cb: Function,
    ctx: unknown
}

export default class EventMgr {
    static instance: any
    public static getInstance(): any {
        if (this.instance == null) {
            this.instance = new EventMgr()
        }
        return this.instance
    }

    private eventMap: Map<string, IItem> = new Map()

    on(IEvt: IItem) {
        const { evt, cb, ctx } = IEvt
        if (!this.eventMap.has(evt)) {
            this.eventMap.set(evt, IEvt)
        }
    }

    off() {

    }

    emit(event: string, ...param) {
        if (this.eventMap.has(event)) {
            const item = this.eventMap.get(event)
            const { evt, cb, ctx } = item
            if (evt && cb) {
                cb.apply(ctx, ...param)
            }
        }
    }

    clear() {

    }
}